body{
    overflow: hidden;
}
a{
    text-decoration: none;
    color: #ccc;
}
.modal_wrap {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5); 
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000; 
  }
  
  .modal {
    background: white;
    padding: 20px;
    border-radius: 8px;
    width: 500px; 
  }
  
  .open_modal{
    cursor: pointer;
    font-size: 24px;
    text-align: right;
    border: none;
    padding: 10px;
    background-color:rgb(58, 142, 184);
  }

  .close_modal {
    cursor: pointer;
    font-size: 24px;
    text-align: right;
  }

  .container{
    margin-bottom: 1000px;
  }
  
  .modal_title {
    margin-bottom: 20px;
  }
  
  .input {
    width: 80%;
    padding: 10px;
    margin: 10px 0;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  .send {
    background-color: #407ed4; 
    color: white;
    border: none;
    cursor: pointer;
  }
